import '../styles/playground/musicBox.scss'
import Art from '../three-js-projects/musical-instrument/art.js';
import { useEffect } from 'react';
import Infobar from './Infobar'
import { designs } from '../util.js'
import ContentItem from '../ContentItem'

function MusicBox() {

  useEffect(() => {

    Art.init();

    window.addEventListener('click', () => {
      Art.addAudio();
    });
    
    return () => {
      Art.removeAllElements();
    };
  }, [])

  return (
    <>
      <div className="musical-container">
        <div className="gui-controls"></div>
        <Infobar></Infobar>
        <h1>MUSIC BOX</h1>
        <canvas id="musicalInstrument"></canvas>
        <p>Click anywhere to unmute. Drag to move the camera. Use the control panel in the top right for some excitement in your life.</p>
      </div>
    </>
  );
}

export default MusicBox;
